import { dictionary } from "./dictionary";

Office.onReady((info) => {
  console.log(`Office está listo. Host: ${info.host}, Plataforma: ${info.platform}`);
  
  const btnNormalizar = document.getElementById("btn-normalizar");
  const statusDiv = document.getElementById("status");
  
  if (btnNormalizar) {
    btnNormalizar.addEventListener("click", normalizarTextoOutlook);
  }
  
  // Función para mostrar mensajes de estado
  window.mostrarEstado = function(mensaje, tipo = 'info') {
    statusDiv.textContent = mensaje;
    statusDiv.className = `status ${tipo}`;
    statusDiv.style.display = 'block';
    
    // Ocultar después de 5 segundos si es éxito
    if (tipo === 'success') {
      setTimeout(() => {
        statusDiv.style.display = 'none';
      }, 5000);
    }
  };
});

// ==========================================
// FUNCIÓN PARA OUTLOOK
// ==========================================
async function normalizarTextoOutlook() {
  try {
    console.log("🔄 Iniciando normalización en Outlook...");
    mostrarEstado("Procesando...", "info");
    
    const item = Office.context.mailbox.item;
    
    // Verificar si estamos en modo de redacción (compose)
    if (item.itemType === Office.MailboxEnums.ItemType.Message) {
      
      // Obtener el cuerpo del correo como HTML
      item.body.getAsync(Office.CoercionType.Html, async (result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          let bodyContent = result.value;
          console.log("📧 Contenido original obtenido");
          
          let cambiosRealizados = 0;
          
          // Aplicar cada regla del diccionario
          for (const rule of dictionary) {
            // Ordenar los matches de más largo a más corto
            const sortedMatches = [...rule.matches].sort((a, b) => b.length - a.length);
            
            // Construir el texto completo esperado con HTML para negrilla
            let expectedTextHTML = "";
            for (const part of rule.replacement) {
              if (part.bold) {
                expectedTextHTML += `<strong>${part.text}</strong>`;
              } else {
                expectedTextHTML += part.text;
              }
            }
            
            console.log(`🔍 Buscando: ${sortedMatches.join(', ')}`);
            console.log(`   Reemplazar con: ${expectedTextHTML}`);
            
            // Buscar y reemplazar cada variante
            for (const match of sortedMatches) {
              // Escapar caracteres especiales de regex
              const escapedMatch = match.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
              
              // Crear regex que busque el texto tanto en texto plano como en HTML
              // Esto captura el texto incluso si está envuelto en tags HTML
              const regex = new RegExp(escapedMatch, 'gi');
              
              // Contar cuántas veces aparece
              const matches = bodyContent.match(regex);
              if (matches) {
                console.log(`  ✨ Encontradas ${matches.length} coincidencia(s) de "${match}"`);
                
                // Reemplazar todas las ocurrencias
                bodyContent = bodyContent.replace(regex, expectedTextHTML);
                cambiosRealizados += matches.length;
              }
            }
          }
          
          // Establecer el nuevo contenido del cuerpo
          item.body.setAsync(bodyContent, { coercionType: Office.CoercionType.Html }, (asyncResult) => {
            if (asyncResult.status === Office.AsyncResultStatus.Failed) {
              console.error("❌ Error al actualizar el cuerpo del correo:", asyncResult.error.message);
              mostrarEstado("Error al actualizar el correo", "error");
            } else {
              if (cambiosRealizados > 0) {
                console.log(`✅ Texto normalizado exitosamente. ${cambiosRealizados} cambio(s) realizado(s).`);
                mostrarEstado(`✅ ${cambiosRealizados} cambio(s) realizado(s)`, "success");
              } else {
                console.log("ℹ️ No se encontraron coincidencias para reemplazar.");
                mostrarEstado("No se encontraron textos que coincidan con el diccionario", "info");
              }
            }
          });
        } else {
          console.error("❌ Error al obtener el cuerpo del correo:", result.error.message);
          mostrarEstado("Error al obtener el contenido del correo", "error");
        }
      });
    } else {
      // En modo de lectura, no podemos editar
      console.log("⚠️ Correo en modo de lectura");
      mostrarEstado("Para normalizar texto, abre un correo nuevo o responde/reenvía", "info");
    }
  } catch (error) {
    console.error("❌ Error en Outlook:", error);
    mostrarEstado("Error: " + error.message, "error");
  }
}
